body{
    font-family: 'Playfair Display', serif;
    margin: 0px;
}
.header {
    overflow: hidden;
    background-color: #272727;
    text-align: center;
    vertical-align: middle;
    font-family: 'PT Sans', sans-serif;
    display: flex;
    align-items: center; 
    justify-content: space-between;
    
  }
  
  .header a {
    display: inline-block;
    color: #F6F6F6;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 18px;
    -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
  }
  
  .logo img {
    width: auto;
    height: auto;
    max-width: 200px;
  }
  
  .header a:hover, .header-menu-item:hover .dropbtn {
    color: #F5E5D9;
    -webkit-filter: blur(0.5px);
  -moz-filter: blur(0.5px);
  -o-filter: blur(0.5px);
  -ms-filter: blur(0.5px);
  filter: blur(0.5px);
  }
  
  .header a.active {
    color: #272727;
    background-color: #F5E5D9;
  }
  
  
  
  .header-menu-item{
    display: inline-block;
    overflow: hidden;
    -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
  }
  .header-menu-item .dropbtn {
    font-size: 20px;
    border: none;
    outline: none;
    color: #F6F6F6;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit; 
    margin: 0;
  }
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #F6F6F6;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1000;
    -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
  }
  .dropdown-content a {
      overflow: hidden;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  .dropdown-content a:hover {
    background-color: #272727;
    color: #F6F6F6;
  }
  .header-menu-item:hover .dropdown-content {
    display: block;
  }

  .swiper-banner {    
    width: 100%;;
    height: 650px;
    background-color: #F5E5D9; 
  }
  .swiper-banner .swiper-slide {
    width: 100%;
    text-align: center;
    display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
}
.swiper-banner .swiper-slide a{
  width: 100%;
}
.swiper-banner .swiper-img {
    width: 100%;
    -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
  }
  .swiper-banner .swiper-img:hover {
    opacity: .5;
}

  .icon-bar {
    overflow: hidden;
    background-color: #272727;
    text-align: center;
    font-family: 'PT Sans', sans-serif;
  }
  .icon-img {
      display: inline-block;      
      padding: 25px;
  }
  .icon-img img{
    border-radius: 50%;
    max-width: 150px;
    height: auto;
    margin-top: 100px;
    margin-bottom: 100px;
    -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}
.icon-img img:hover{
    opacity: .5;
}

.row {
  background-color: #F5E5D9;
  color: #272727;
  padding: 25px;
  display: flex; 
  justify-content: space-around;
  max-height: 600px;
}
.row h1 {
  font-family: 'PT Sans', sans-serif;
}
.page-article {
  max-width: 40%;
  padding: 10px;
  margin: 10px;
}
.banner-row{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.banner{
  background-color: #272727;
  text-align: center;
  margin: 0 10px 10px 0;
}
.banner img {
  width: 400px;
  height: auto;
}
.banner-text {
  padding: 20px;
  color: #F5E5D9;
}
.banner-text h2{
  font-family: 'PT Sans', sans-serif;
}

.post-row{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 750px;
  background: linear-gradient(90deg, #F5E5D9 85%, white 15%);
  color: #272727;
  overflow: visible;
  padding: 20px;
}
.post-card{
  display: flex;
  align-items: flex-start;
 
  text-align: justify;
  width: 90%;
}
.post-card img {
  margin: 20px;
}
.post-card .post-text {
  margin: 20px;
  padding-left: 20px;
  padding-right: 20px;
}
.post-text p{
  
}
.post-text h2{
  font-family: 'PT Sans', sans-serif;
}
.post-text button{
  border: none;
  color: #F6F6F6;
  background-color: #272727;
  border: 2px solid #272727;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
}
.post-text button:hover{
  background-color: #F6F6F6;
  color: #272727;
  border-radius: 0%;
}


.mini-swiper {    
  width: 600px;
  height: 300px;
  background-color: #F5E5D9;   
}
.mini-swiper .swiper-slide {
  display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    justify-content: ;
}
.mini-swiper .swiper-img {
  width: 100%;
  height: 300px;
  height: 300px;;
  -webkit-transition: .3s ease-in-out;
transition: .3s ease-in-out;
}
.mini-swiper .swiper-img:hover {
  opacity: .5;
}

.insta-row {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin-top: 80px;
}
.insta-row h1{
  font-family: 'PT Sans', sans-serif;
  color: #272727;
  margin: 0px;
  padding: 0px;
}
.insta-gallery {
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: space-around;
  
}
.insta-img {
}
.insta-img img {
  width: 400px;
}
.insta-txt {
  align-self: flex-end;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: justify;
  font-size: 11pt;
  width: 400px;
  height: 400px;
  background-color: #272727;
}
.insta-txt p {
  padding: 30px;
  color: #F5E5D9;
}


.footer-row {
  margin-top: 300px;
}
.footer-head{
  background-color: #272727;
  height: 2px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
  margin-bottom: 100px;
}
.footer-head img {
  width: 200px;
}
.footer-content {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  padding: 0px;
}
.side-menu{

}
.side-menu h1{
  font-family: 'PT Sans', sans-serif;
  color: #272727;
  padding: 14px 16px;
}
.menu-content{
  width: 100%;
  display: block;
}
.menu-content a{
  display: inline-block;
    color: #272727;
    border: 2px solid white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 18px;
    -webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}
.menu-content a:hover{
  background-color: #F6F6F6;
  border-radius: 0%;
  border-color: #272727;
}
.column-menu a{
  display: inline-block;
  width: 100%;
    color: #272727;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 18px;
}
.column-menu a:hover{
  text-decoration: underline;
}
.center-menu{
  display: block;
  width: 500px;  
  padding: 14px 16px;
  color: #272727;
}
.center-menu h1 {
  font-family: 'PT Sans', sans-serif;
  
}
.center-content {
  width: 100%;
  border-bottom: 2px solid #272727;
  padding: 7px;
}
.center-content input {
  width: 80%;
  font-family: 'PT Sans', sans-serif;
  border: none;
  font-size: 16px;  
  outline: none;
}
.center-content button {
  border: none;
  color: #272727;
  background-color: white;
  text-decoration: none;
  font-size: 16px;
  padding: 7px;
}
.center-content button:after {
  border: none;
}
.footer-end {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #272727;
  margin-bottom: 50px;
}
.footer-end p{
  font-size: 18dp;
}
.footer-end a{
  text-decoration: none;
  color: #272727;
}




  /*MOBILE e TABLETS*/
  @media screen and (max-width: 768px) {
    .header a {
      float: none;
      display: block;
      text-align: left;
    }
    .logo {
        display: block;
        float: none;
        width: 100%;
    }
    .header-menu-item .dropbtn{
        font-size: 16px;
    }
    .header-right {
      float: none;
      text-align: center;
    }
    .header-right a {
      display: inline-block;
    }
    .swiper-banner {    
        height: 300px;          
      }
      .swiper-banner .swiper-slide a{
        width: 100%;
      }
      .swiper-banner .swiper-img{
        width: 100%;
      }
      .icon-img img{        
        margin-top: 5px;
        margin-bottom: 5px;
    }
    .row {
      max-height: none;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .page-article {
      max-width: 100%;
    }
    
    .banner-row{
      width: 100%;
    }
    .banner {      
      width:100%;      
    }
    .banner img{
      width: 100%;
    }

    .post-row{
      flex-direction: column;
      margin-top: 10px;
      background: white;
      padding: 0px;
      width: 100%;
      height: fit-content;
    }
  
    .post-card{
      flex-direction: column;
      align-items: center;
      padding: 10px;
      width: 100%;
    }
    .post-card img {
      margin: 10px;
    }
    .post-card .post-text {
      margin: 10px;
      padding: 0px;
    }
    .mini-swiper{
      width: 100%;
    }

    .insta-row { 
      display: inline-block;
      height: fit-content;
      text-align: center;
    }
    .insta-row h1{
      margin: 10px;
    }
    .insta-gallery {
      width: 100%;
      display: inline-block;
      text-align: center;
      margin: 0px;
      padding: 0px;
    }
    .insta-img {
      width: 100%;
    }
    .insta-img img {
      width: 100%;
    }
    .insta-txt {
      margin-bottom: 5px;
      width: 100%;
    }
    
    .footer-content {
      width: 100%;
      display: inline-block;
      text-align: center;
    }
    
    .menu-content{
      width: 100%;
      display: flex;
      justify-content: center;
    }
    
    .column-menu {
      display: flex;
    }
    
    .center-menu{
      display: block;
      width: 100%;  
      padding: 0px;
      margin-top: 40px;
      text-align: center;
    }    
    .center-content {
      border: none;
      display: flex;
      justify-content: center;
    }
    .center-content input {
      width: 70%;
      border-bottom: 2px solid #272727;
    }
    .center-content button {
      border: none;
      color: #F6F6F6;
      background-color: #272727;
      text-decoration: none;
      font-size: 16px;
      padding: 7px;
    }
    .center-content button:after {
      border: none;
    }
    .footer-end{
      margin-top: 80px;
      text-align: center;
    }
    .footer-end p{
      align-self: center;
      font-size: 15px;
    }
    
  }